<template>
    <div class ="login">
      <div>
        <h2>Log In</h2>
        <div id="loginPage">
          <form @submit.prevent="Loginform">
            <p>
              <input class="w3-input" name="id" placeholder="Enter your ID" v-model="id"><br>
            </p>
            <p>
              <input name="password" class="w3-input" placeholder="Enter your password" v-model="password" type="password">
            </p>
            <p>
              <input name="email" class="w3-input" placeholder="Enter the email you want to change" v-model="email">
            </p>
            <p>
              <button type="submit" class="w3-button w3-green w3-round">수정</button>
            </p>
          </form>
        </div>
      </div>
    </div>
  </template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
        loginSuccess: false,
        loginError: false,
        id: '',
        password: ''
    }
  },
  methods: {
    async Loginform() {
      if (this.id === '') {
        alert('ID를 입력하세요.')
        return
      }

      if (this.password === '') {
        alert('비밀번호를 입력하세요.')
        return
      }

      if (this.email === '') {
        alert('변경할 이메일을 입력하세요.')
        return
      }
        let loginData = {};
            loginData.id = this.id;
			loginData.password = this.password;
            loginData.email = this.email;
        try {
				axios.post('/member/update', JSON.stringify(loginData), {
				headers: {
					"Content-Type": `application/json`,
					},
					})
				.then((response) => {
					console.log(response)
						alert("회원정보가 변경 되었습니다. 회원목록으로 돌아갑니다")
						this.$router.push({path: '/about'});
					})
				.catch(error =>{
				console.log(error.response);
          alert("회원정보 수정에 실패했습니다. 아이디와 비밀번호를 확인해주세요.")
                this.$router.push({path: '/update'});
				});
		} catch (error) {
			console.error(error);
		}
      
    }
  }
}
</script>

<style>
#loginForm {
  width: 500px;
  margin: auto;
}
</style>